<template>
  <div class="fix-header card-no-border fix-sidebar">
    <div id="main-wrapper">
      <Header />
      <div
        class="page-wrapper"
        style="min-height: 324px;"
      >
        <div class="container-fluid">
          <div class="row page-titles">
            <div class="col-md-6 col-8 align-self-center">
              <h3 class="text-themecolor mb-0 mt-0">
                Заявки на выплату
              </h3>
              <ol class="breadcrumb">
                <li class="breadcrumb-item">
                  <a href="javascript:void(0)">Главная</a>
                </li>
                <li class="breadcrumb-item active">
                  Заявки на выплату
                </li>
              </ol>
            </div>
          </div>
          <div class="row">
            <div class="col-12">
              <PaymentRequestFilter />
              <div class="card">
                <div class="table-responsive m-t-40">
                  <div
                    id="payment-request_wrapper"
                    class="dataTables_wrapper container-fluid dt-bootstrap4 no-footer"
                  >
                    <div class="row">
                      <div class="col-sm-12 col-md-6">
                        <div
                          id="payment-request_length"
                          class="dataTables_length"
                        >
                          <label>Отображено
                            <select
                              v-model.number="limit"
                              name="payment-request_length"
                              aria-controls="payment-request"
                              class="form-control form-control-sm"
                            >
                              <option value="10">10</option>
                              <option value="25">25</option>
                              <option value="50">50</option>
                              <option value="100">100</option>
                            </select>
                            записей на страницу
                          </label>
                        </div>
                      </div>
                      <div class="col-sm-12 col-md-6">
                        <div
                          id="payment-request_filter"
                          class="dataTables_filter"
                        >
                          <label>Поиск:<input
                            type="search"
                            class="form-control form-control-sm"
                            placeholder=""
                            aria-controls="payment-request"
                          ></label>
                        </div>
                      </div>
                    </div>
                    <div class="row">
                      <div class="col-sm-12">
                        <PaymentRequestTable />
                      </div>
                    </div>
                    <PaymentRequestAction />
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <Footer />
      </div>
    </div>
  </div>
</template>

<script>
import { mapActions, mapMutations, mapState } from 'vuex';

import Header from '@/components/Header';
import Footer from '@/components/Footer';
import PaymentRequestTable from '@/components/payment_request/PaymentRequestTable';
import PaymentRequestFilter from '@/components/payment_request/PaymentRequestFilter';
import PaymentRequestAction from '@/components/payment_request/PaymentRequestAction';

export default {
  name: 'PaymentRequest',
  components: {
    PaymentRequestAction,
    PaymentRequestFilter,
    PaymentRequestTable,
    Footer,
    Header,
  },
  data: () => ({
    showPreloader: false,
    select_group: null,
    select_group_options: [
      { value: 'a', text: 'Группа 1' },
      { value: 'b', text: 'Группа 2' },
    ],
    select_type: null,
    select_type_options: [
      { value: 'a', text: 'Заявка парка' },
      { value: 'b', text: 'Заявка водителя' },
    ],
  }),
  computed: {
    limit: {
      get() {
        return this.$store.state.paymentRequests.limit;
      },
      set(newValue) {
        this.setLimit(newValue);
        this.setPage(1);
        this.getPaymentRequests();
      },
    },
    ...mapState('paymentRequests', ['paymentRequests']),
  },
  mounted() {
    this.getPaymentRequests();
  },
  methods: {
    ...mapMutations('paymentRequests', ['setLimit', 'setPage']),
    ...mapActions('paymentRequests', ['getPaymentRequests']),
  },
};
</script>
